.header {
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 30px;
    font-weight: 600;
    margin: 0;
}

.h1 {
    margin: 0;
    font-size: 24px;
}

.button {
    padding: 9px 25px;
    background-color: black;
    color: white;
}

.button:hover {
    background-color: transparent;
    color: black;
}

.buttonLogOut {
    padding: 9px 25px;
    background-color: transparent;
    color: black;
}

.buttonLogOut:hover {
    background-color: black;
    color: white;
}



.a {
    text-decoration: none;
    color: black;
    margin-right: 30px;
}

.a:hover {
    border-bottom: solid black 1.5px;
}