.page {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.imgContainer {
    margin-right: 50px;
    width: 50%;
}

.formDiv {
    margin-left: 100px;
}

.formWrapper {
    width: 60%;
}

.error {
    color: red;
    margin-top: 5px;
    margin-bottom: 10px;
}


.formDiv h3 {
    margin-bottom: 30px;
}

.formDiv input,
.formDiv select {
    font-family: 'Overpass', sans-serif;
    font-size: 16px;
    padding: 20px;
    width: 90%;
    border-radius: 5px;
    border: solid #7A7A7A 1px;
    margin-bottom: 10px;
}

.formDiv select {
    color: #7A7A7A;
}


[type="date"]::-webkit-inner-spin-button {
    display: none;
}

[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
}

input[type=date]::-webkit-datetime-edit {
    color: #7A7A7A;
}

.formDiv select:focus {
    color: black;
    border: solid black 1px;
}

.formDiv input:focus {
    border: solid black 1px;
}

.formDiv input:valid {
    border: solid #84D0AB 1px;
}

.formDiv select:invalid {
    border: solid darkred 1px;
}

.formDiv select:valid {
    color: black;
    border: solid #84D0AB 1px;
}


.formDiv input:optional {
    border: solid #7A7A7A 1px;
}

.formDiv input:invalid {
    border: solid darkred 1px;
}


.formDiv button {
    margin-top: 15px;
    padding: 15px 30px;
}

.formDiv button:hover {
    color: white;
}

@media (max-width: 1150px) {
    .imgContainer {
        display: none;
    }
    .formWrapper {
        width: 80%;
    }

}