.opportunities {
    margin-left: 100px;
}

.grid {
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(250px, 1fr));
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    width: 100%;
}

.gridHero {
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(400px, 1fr));
    grid-column-gap: 60px;
    grid-row-gap: 60px;
    width: 100%;
}


@media (max-width: 740px) {

    .gridHero {
        grid-template-columns: 1fr;
        grid-column-gap: 40px;
        grid-row-gap: 40px;
        width: 100%;
    }

}