.img {
    border-radius: 5%;
    width: 100%;
    height: 100%;
}

.h4 {
    font-size: 22px;
    margin: 10px 0 5px 0;
}

.postedBy {
    margin: 0;
    font-weight: 500;
}

.a {
    text-decoration: none;
    padding-bottom: -2px;
    border-bottom: solid black 1.5px;
}

.a:hover {
    border-bottom: none;
}

.desc {
    color: #7A7A7A;
    font-weight: 300;
}

.aDesc {
    font-weight: 500;
}

.arrow {
    border: solid black;
    border-radius: 0.5px;
    border-width: 0 1.5px 1.5px 0;
    display: inline-block; 
    padding: 3px;
    margin-left: 5px;
}
  
.right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}

.icon {
    transition: all 0.5s ease-out;
    display: inline-block;
}

.discover:hover .icon {
    transform: translateX(20px);
}

