.hero {
    padding-top: 100px;
    background-color: #FFE2B4;
    height: 100vh;
    margin: 0;
    padding: 15px 100px;
    display: flex;
    align-items: center;
}

.h3 {
    margin: 0px 0 30px 0;
}


.divimage {
    width: 37vw;
    height: 100%;
}

.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.button {
    padding: 15px 30px;
}

.button:hover {
    color: #FFE2B4;
}

.rightSection {
    width: 40%;
}


@media (max-width: 950px) {
    .divimage {
        display: none;
    }
    .rightSection {
        width: 80%;
    }

    .hero {
        padding: 15px 50px 15px 100px;
    }

  }